.host-view-vis-dc {
  border: 2px dotted #CCC;
  margin: 16px;
}
.host-view-vis-dc-title {
  margin: 16px;
}
.host-view-vis-dc-content {
  margin: 16px;
}
.host-view-vis-host {
  padding: 16px 24px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.host-view-vis-host-content {
  flex: 1 1;
}
.host-view-vis-host.clickable {
  cursor: pointer;
}
.host-view-vis-host.clickable:hover {
  background: #ebfbee;
}
